@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import './mixin.scss';

body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

nav {
  margin: 0 auto;
  width: 80%;
  height: 8vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  @include media('<=phone') {
    display: block;
    text-align: center;
    height: auto;
    a {
      margin-top: 10px;
      margin-bottom: 20px;
    }
  }
}

a {
  font-size: 1rem;
  text-decoration: none;
  color: #202021;
  font-weight: 800;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
li {
  margin: 0;
  padding: 0;
}
.side-nav {
  position: fixed;
  top: 8vh;
  left: 1%;
  box-sizing: border-box;
  padding: 10px;
  background-color: #3d2ab7;

  ul {
    display: flex;
    flex-direction: column;
    li {
      margin-bottom: 10px;
      a {
        color: white;
        &:hover {
          font-size: 1.5rem;
        }
      }
    }
  }
}
.home {
  .container {
    width: 75%;
    margin: 0 auto;
    .heading {
      h1 {
        margin-top: 30px;
        text-transform: uppercase;
      }
      .f-image {
        width: 100%;
        .wrapper {
          display: grid;
          grid-template-columns: repeat(2, 1fr);

          .image {
              width: 100%;
            img {
                display: block;
                width: 100%;
                object-fit: cover;
            }
          }
          @include media('<=tablet') {
            .second {
              display: none;
            }
          }
          @include media('<=phone') {
            grid-template-columns: 1fr;
          }
        }
      }
    }
  }
}
